import React, { useState } from 'react';

export type NetworkFilter = 'all' | 'solana' | 'ethereum' | 'basic' | 'license';
export type SortFilter = 'market_cap' | 'volume_24h' | null;

interface CoinFilterBarProps {
  selectedNetwork: NetworkFilter;
  onNetworkChange: (network: NetworkFilter) => void;
  sortBy: SortFilter;
  onSortChange: (sort: SortFilter) => void;
  onFiltersClick: () => void;
  onColumnsClick: () => void;
}

const CoinFilterBar: React.FC<CoinFilterBarProps> = ({
  selectedNetwork,
  onNetworkChange,
  sortBy,
  onSortChange,
  onFiltersClick,
  onColumnsClick,
}) => {
  const [showMoreDropdown, setShowMoreDropdown] = useState(false);

  const networks = [
    { id: 'all' as NetworkFilter, label: 'T√ºm Aƒülar', icon: 'üåê', color: 'from-gray-500 to-gray-600' },
    { id: 'license' as NetworkFilter, label: 'Lisans', icon: '‚≠ê', color: 'from-yellow-500 to-amber-600' },
    { id: 'solana' as NetworkFilter, label: 'Solana', icon: 'üíú', color: 'from-purple-500 to-purple-600' },
    { id: 'basic' as NetworkFilter, label: 'Temel', icon: 'üíé', color: 'from-blue-500 to-blue-600' },
    { id: 'ethereum' as NetworkFilter, label: 'Ethereum', icon: 'üî∑', color: 'from-indigo-500 to-indigo-600' },
  ];

  const sortOptions = [
    { id: 'market_cap' as SortFilter, label: 'Piyasa Deƒüeri', icon: 'üìä' },
    { id: 'volume_24h' as SortFilter, label: 'Hacim (24 saat)', icon: 'üìà' },
  ];

  return (
    <div className="bg-gradient-to-r from-blue-50 via-purple-50 to-pink-50 px-4 py-4 sticky top-0 z-40 border-b border-gray-200">
      <div className="max-w-7xl mx-auto">
        <div className="flex flex-nowrap items-center gap-3 md:gap-4 overflow-x-auto scrollbar-hide">
          {/* Sol taraf - Network filtreleri */}
          <div className="flex items-center gap-2 md:gap-3 flex-nowrap shrink-0">
            {networks.map((network) => (
              <button
                key={network.id}
                onClick={() => onNetworkChange(network.id)}
                className={`
                  flex items-center justify-center gap-2 px-4 py-2 h-10 rounded-xl text-sm font-semibold transition-all duration-200 transform hover:scale-105
                  ${
                    selectedNetwork === network.id
                      ? `bg-gradient-to-r ${network.color} text-white shadow-lg`
                      : 'bg-white text-gray-700 hover:bg-gray-100 shadow-sm border border-gray-200'
                  }
                `}
              >
                <span className="text-lg leading-none">{network.icon}</span>
                <span className="hidden sm:inline leading-none">{network.label}</span>
                <span className="sm:hidden leading-none">{network.label.split(' ')[0]}</span>
              </button>
            ))}

            {/* Daha fazla dropdown */}
            <div className="relative">
              <button
                onClick={() => setShowMoreDropdown(!showMoreDropdown)}
                className="flex items-center justify-center gap-1 px-4 py-2 h-10 rounded-xl text-sm font-semibold bg-white text-gray-700 hover:bg-gray-100 transition-all shadow-sm border border-gray-200 transform hover:scale-105"
              >
                <span>Daha</span>
                <svg
                  className={`w-4 h-4 transition-transform ${showMoreDropdown ? 'rotate-180' : ''}`}
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
              </button>

              {showMoreDropdown && (
                <>
                  <div
                    className="fixed inset-0 z-10"
                    onClick={() => setShowMoreDropdown(false)}
                  />
                  <div className="absolute top-full left-0 mt-2 w-48 bg-white rounded-xl shadow-2xl py-2 z-20 border border-gray-100">
                    <button className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gradient-to-r hover:from-blue-50 hover:to-purple-50 transition-colors">
                      üî∑ Polygon
                    </button>
                    <button className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gradient-to-r hover:from-blue-50 hover:to-purple-50 transition-colors">
                      üíõ BSC
                    </button>
                    <button className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gradient-to-r hover:from-blue-50 hover:to-purple-50 transition-colors">
                      ‚ùÑÔ∏è Avalanche
                    </button>
                  </div>
                </>
              )}
            </div>
          </div>

          {/* Saƒü taraf - Sƒ±ralama ve filtreler */}
          <div className="flex items-center gap-2 md:gap-3 ml-auto shrink-0">
            {/* Sƒ±ralama filtreleri */}
            {sortOptions.map((option) => (
              <button
                key={option.id}
                onClick={() => onSortChange(sortBy === option.id ? null : option.id)}
                className={`
                  flex items-center justify-center gap-2 px-4 py-2 h-10 rounded-xl text-sm font-semibold transition-all duration-200 transform hover:scale-105
                  ${
                    sortBy === option.id
                      ? 'bg-gradient-to-r from-purple-600 to-pink-600 text-white shadow-lg'
                      : 'bg-white text-gray-700 hover:bg-gray-100 shadow-sm border border-gray-200'
                  }
                `}
              >
                <span className="text-base leading-none">{option.icon}</span>
                <span className="hidden sm:inline leading-none">{option.label}</span>
                <span className="sm:hidden leading-none">{option.label.includes('Piyasa') ? 'Piyasa' : 'Hacim'}</span>
              </button>
            ))}

            {/* Filtreler butonu */}
            <button
              onClick={onFiltersClick}
              className="flex items-center justify-center gap-2 px-4 py-2 h-10 rounded-xl text-sm font-semibold bg-white text-gray-700 hover:bg-gray-100 transition-all shadow-sm border border-gray-200 transform hover:scale-105"
            >
              <span className="leading-none">üîç</span>
              <span className="hidden sm:inline leading-none">Filtreler</span>
              <span className="sm:hidden leading-none">Filtre</span>
            </button>

            {/* S√ºtunlar butonu */}
            <button
              onClick={onColumnsClick}
              className="flex items-center justify-center gap-2 px-4 py-2 h-10 rounded-xl text-sm font-semibold bg-white text-gray-700 hover:bg-gray-100 transition-all shadow-sm border border-gray-200 transform hover:scale-105"
            >
              <span className="leading-none">üìã</span>
              <span className="hidden sm:inline leading-none">S√ºtunlar</span>
              <span className="sm:hidden leading-none">S√ºtun</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CoinFilterBar;
